<template>
  <div>
    <!-- Navigation -->
    <div class="short-header">
      <Header />
      <div class="container pt-5">
        <div class="mt-5 pb-5">
          <h1 class="h-t-h mt-5 m-b-3 text-center text-light">Portfolio</h1>
        </div>
      </div>
    </div>
    <!-- welcome -->
    <div class="container">
      <div class="row mb-5">
        <div class="col-lg-12 text-center">
          <div class="row my-4">
            <div class="col-md-12">
              <h3 class="mt-5 font-weight-bold text-uppercase s-head">
                Our latest work in different technologies
              </h3>
              <p class="n-d-t mt-4 text-left">
               We don’t have much to say, our portfolio speaks it all. It is crafted by our skilled employees with care. It contains projects from diverse technologies and domains. Please have a look.
          </p>
            </div>
          </div>
        </div>
        <div class="col-lg-12 text-center" >
        
            <!-- Nav tabs -->
            <ul class="nav nav-tabs" role="tablist">              
              <li class="nav-item" v-for="(item, index) in portfolioData" :key="index">
                <a class="nav-link portfolio-nav-link" v-bind:class="{ 'active':(index == 0) }" data-toggle="tab" :href="'#tab-'+item.service_id">{{ item.service_name }}</a>
              </li>   
               <button
                v-if="backbutton"
                type="button"
                v-on:click="Filterclose()"
                class="close m-auto"
              >
                <span aria-hidden="true">x</span>
              </button>           
            </ul>

            <!-- Tab panes -->
            <div class="tab-content">
              <div :id="'tab-'+item.service_id" class="container tab-pane" v-bind:class="{ 'active':(index == 0) }" v-for="(item, index) in portfolioData" :key="index"> 
                <div class="row mt-3">                  
                  <div class="col-md-4 mt-3" v-for="(project, project_index) in item.projects" :key="project_index">
                    <div class="set-container">
                     <img
                          v-if="project.logo != ''"
                          v-on:click="projectdata(project.slug)"
                          v-bind:src="project.logo"
                          class="w-100 p-0 set-shadows set h-w-fix"
                          alt="Nature"
                          style="width: 100%"
                        />
                        <img
                          v-if="project.logo == ''"
                          v-on:click="projectdata(project.slug)"
                          src="../assets/images/images.png"
                          class="w-100 p-0 set-shadows set h-w-fix"
                          alt="Nature"
                          style="width: 100%"
                        />
                      <div class="top-left set-border col-md-8 py-1 px-1 bg-light text-uppercase font-weight-bold pointer" v-on:click="projectdata(project.slug)">
                        {{ project.project_title }}
                      </div>
                      <div class="row mt-2 mx-0 w-100 text-block">
                        <div class="col-md-12">
                          <div class="row m-0" v-if="project.skills.length > 0">
                            <div class="text-left">
                                <p class="m-0 p-s-f w-w-d-s p-1 bg-blue font-weight-bold" v-for="(list, skill_index) in project.skills" :key="skill_index">
                                  <span v-on:click="ProjectName(list.skill_id)" class="pointer">
                                    {{ list.name }}
                                    </span>
                                </p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>                    
                  </div>                  
                </div>
              </div>              
            </div>
            <slick ref="slick" :options="slickOptions" v-if="portfolioData.length > 0">
              
            </slick>
        </div>
      
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Slick from "vue-slick";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";
Vue.use(VueAxios, axios);
export default {
  name: "Portfolio",
  data() {
    return {
      portfolioData: [],
      backbutton:false,
      slickOptions: {
        slidesToShow: 3,
        slidesToScroll: 1,
        infinite: true,
        arrows: false
        // Any other options that can be got from plugin documentation
      },
      slickOption: {
        slidesToShow: 3,
        slidesToScroll: 1,
        infinite: true,
        arrows: false
        // Any other options that can be got from plugin documentation
      }
    };
  },
  mounted() {
      this.getPortfolioData();
  },
  components: {
    Header,
    Footer,
    Slick
  },
   methods: {    
    async getPortfolioData() {
      this.backbutton=false;
      if(typeof(Vue.prototype.$portfolioData) == "undefined") {
      var param = "lang=" + "0";
      let res = await apiServices.makeAPICall(
        apiServices.postMethod,
        apiServices.URL + apiServices.portfolio_dataAPI,
        param,
        () => {}
      );    
      Vue.prototype.$portfolioData = res.data.data;
      this.portfolioData = Vue.prototype.$portfolioData;   
      }
      else {
        this.portfolioData = Vue.prototype.$portfolioData;
      }   
    },
    projectdata(title) {
      this.$router.push("/portfolio-details/" + title);
      // localStorage.projectid = id;
    },
    reInit() {
      this.$nextTick(() => {
        this.$refs.slick.reSlick();
      });
    },
    async ProjectName(id){
      this.backbutton=true;
      var param = "lang=" + "0"+"&search_keyword=" + id;
      let res = await apiServices.makeAPICall(
        apiServices.postMethod,
        apiServices.URL + apiServices.portfolio_search_dataAPI,
        param,
        () => {}
      );   
      this.portfolioData = res.data.data;
    },
    async Filterclose(){
      this.backbutton=false;
      this.getPortfolioData();
    }
  },
};
</script>

<style>
.set-container {
  position: relative;
  font-family: Arial;
}

.text-block {
  position: absolute;
  bottom: 20px;
}
.top-left {
  position: absolute;
  top: 8px;
  left: 16px;
}
.set-border {
  border-left: 7px solid #056ab1;
}
.set-shadows {
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.2) !important;
}
.p-s-f {
  font-size: 12px;
  width: fit-content;
  display: inline;
  margin: 0px 2px !important;
}
.remove-focus {
  outline: none;
}
.portfolio-nav-link {
  color: #495057;
  padding: 0.6rem 2rem;
  font-size: 18px;
}
.h-w-fix {
  width: 100% !important;
  height: 249px !important;
  max-height: 249px !important;
}
.m-auto {
  margin: auto;
}
</style>