import Vue from "vue";
import VueRouter from "vue-router";

import Home from "../Home.vue";
import Service from "../Service.vue";
import Aboutus from "../Aboutus.vue";
import Protfolio from "../Protfolio.vue";
import protfoliodetails from "../Protfoliodetails.vue";
import Blog from "../Blog.vue";
import Contactus from "../Contactus.vue";
import Jobopening from "../Jobopening.vue";
import Lifedignizant from "../Lifedignizant.vue";
import Blogdetails from "../Blogdetails.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "home",
        component: Home
    },
    {
        path: "/service/:id",
        name: "service",
        component: Service
    },
    {
        path: "/about-us",
        name: "aboutus",
        component: Aboutus
    },
    {
        path: "/portfolio",
        name: "Protfolio",
        component: Protfolio
    },
    {
        path: "/portfolio-details/:id",
        name: "Protfoliodetails",
        component: protfoliodetails
    },
    {
        path: "/blog",
        name: "Blog",
        component: Blog
    },
    {
        path: "/contact-us",
        name: "Contactus",
        component: Contactus
    },
    {
        path: "/job-openings",
        name: "Jobopening",
        component: Jobopening
    },
    {
        path: "/life-at-dignizant",
        name: "Lifedignizant",
        component: Lifedignizant
    },
    {
        path: "/blog-details/:id",
        name: "Blogdetails",
        component: Blogdetails
    },
];

const router = new VueRouter({
    mode: 'history',
    routes,
    scrollBehavior() {
        return { x: 0, y: 0 }
    }
});

export default router;