                <li>Trouble-free customer service</li>
                <li>Custom made solutions</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="d-block mt-5 pb-5 bg-odd">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5 font-weight-bold s-head mb-5">What We Do</h1>
            <div class="row mt-2">
              <div class="col-md-12">
                <div class="row">
                  <div class="col-md-4 p-2" v-for="(item, index) in Servicedata" :key="index">
                    <div class="w-w-d-s">
                      <div class="f-h-i h-150">
                        <img v-bind:src="item.icon" />
                      </div>
                      <h4 class="mt-4 mb-4 pl-5 pr-5 l-h-35 font-weight-bold text-uppercase">
                        {{ item.title }}
