                      </p>
                      <!-- <p class="mb-0">
                        {{item.description.slice(0,160)}}
                        <span v-if="item.description.length>160">...</span>
                      </p> -->
                      <a v-on:click="Viewmore(item.slug)" class="btn btn-primary text-light s-v-m-b mt-4 mb-2">
                        View More
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="d-block">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5 font-weight-bold s-head mb-4">How We Do It</h1>
            <p class="n-d-t mt-4">
              We believe in efficient work, high quality products and unforgettable customer experiences. As soon as a client reaches us, we take all necessary requirements. Our research team then takes charge and completes all the research work needed and suggests the best possible solution for their problem. Our team then start building the product and passes it through various phases in order to give life to your product.
            </p>
            <img src="../assets/images/hwdi.png" class="mt-5" width="100%" />
          </div>
        </div>
      </div>
    </div>
    <div class="d-block mt-5">
      <img src="../assets/images/img-2.png" width="100%" />
    </div>
    <div class="d-block bg-odd pb-5">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5 font-weight-bold s-head mb-5">
              Business Verticals
            </h1>
            <div class="row mt-2">
              <div class="col-sm">
                <img src="../assets/images/education.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">EDUCATION</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/financial-services.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">FINANCIAL SERVICES</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/healthcare.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">HEALTHCARE</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/healthcare.png" class="" />
