              </div>
              <div class="col-sm">
                <img src="../assets/images/real-estate.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">REAL ESTATE</h5>
              </div>
            </div>
            <div class="row mt-4">
              <div class="col-sm">
                <img src="../assets/images/manufacturing.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">MANUFACTURING</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/social-marketing.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">SOCIAL MARKETING</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/entertainment.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">ENTERTAINMENT</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/ecommerce.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">E-COMMERCE</h5>
              </div>
              <div class="col-sm">
                <img src="../assets/images/utilities.png" class="" />
                <h5 class="font-weight-bold b-v-t mt-3">UTILITIES</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Start Our Latest Work -->
    <OurLatestWorkSlider />

    <!-- Start What Our Client Says -->
    <div class="d-block pb-5">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5 font-weight-bold s-head mb-5">
              What Our Client Says
            </h1>            
            <div>
              <slick ref="slick" :options="slickOptions" v-if="testimonialsData.length > 0">
                <a v-for="(item, index) in testimonialsData" :key="index">
                  <p class="px-5 n-d-t">
                    {{ item.comment }}
                  </p>
                  <hr class="line-blue" />
                  <p class="author font-weight-bold">{{ item.name }}</p>
                </a>
              </slick>
              <button @click="prev" class="prev">
                <img
                  class="left-arrow"
                  src="../assets/images/ic_left_arrow.png"
                  alt=""
                />
              </button>
              <button @click="next" class="next">
                <img
                  class="right-arrow"
                  src="../assets/images/ic_right_arrow.png"
                  alt=""
                />
              </button>
            </div>
            <div></div>
          </div>
        </div>
      </div>
