    {{ dataresponse }}
    <Footer />
  </div>
</template>
  <script type="text/javascript" src="https://code.jquery.com/jquery-1.11.3.min.js"></script>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import OurLatestWorkSlider from "./OurLatestWorkSlider.vue";
import Slick from "vue-slick";
import { VueTyper } from 'vue-typer'
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";
import $ from 'jquery';
// import router from "../components/router";

Vue.use(VueAxios, axios);

export default {
  name: "Home",
  data() {
    return {
      lang: 0,
      selected: '',
      username: '',
      useremail: '',
      usersfileelected: '',
      userselected: '',
      usermessage: '',
      user: {},
      Servicedata: [],
      testimonialsData: [],
      dataresponse: "",
      slickOptions: {
        slidesToShow: 1,
        slidesToScroll: 1,
        infinite: true,
        arrows: false,        
      },      
    };
  },
  mounted() {
    this.getServicesdata();
    this.getTestimonials();
  },
  components: {
    Header,
    Footer,
    Slick,
    VueTyper,
    OurLatestWorkSlider,
  },
  methods: {
    async getTestimonials() {
      var param = "lang=" + "0";
      let res = await apiServices.makeAPICall(
        apiServices.postMethod,
        apiServices.URL + apiServices.testimonials_dataAPI,
        param,
        () => {}
      );
      this.testimonialsData = res.data.data;
    },
    async getServicesdata() {
      if(typeof(Vue.prototype.$servicesDatas) == "undefined") {
        var param = "lang=" + "0";
        let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.home_services_dataAPI,
          param,
          () => {}
        );
        Vue.prototype.$servicesDatas = res.data.data;
        this.Servicedata = Vue.prototype.$servicesDatas;
      }
      else {
        this.Servicedata = Vue.prototype.$servicesDatas;
      }
    },
    
    onFileChanged(event) {
      let input = event.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          this.user.photo = e.target.result;
          this.selectedFile = input.files[0];
        };
        reader.readAsDataURL(input.files[0]);
      }
console.log(this.selectedFile,event.target.files[0]);
      // this.selectedFile = event.target.files[0];
    },
    async submit(){
      this.username = "";
      this.useremail = "";
      this.userselected = "";
      this.usermessage = "";
      this.usersfileelected = "";

       if (!this.user.name) {
          this.username ='Please Enter Your Name';
       }
       if (!this.user.email) {
          this.useremail ='Please Enter Your Email';
       }
        else if(!this.validEmail(this.user.email)) {
          this.useremail = 'Not a valid Email';
       }
       if (!this.selected) {
          this.userselected ='Please Select';
       }
        if (!this.selectedFile) {
          this.usersfileelected ='Please Upload File';
       }
       if (!this.user.message) {
          this.usermessage ='Please Enter Message';
       }
       if ( this.username == "" && this.useremail == "" && this.userselected == "" && this.usersfileelected == "" && this.usermessage == "") {
     
      let formData = new FormData();
      formData.append("name", this.user.name);
      formData.append("email", this.user.email);
      formData.append("lang", '0');
      formData.append("service_id", this.selected);
      formData.append("message", this.user.message);
     
      if (this.selectedFile) {
        formData.append("doc_file", this.selectedFile);
      }
       let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.add_quoteAPI,
          formData,
          () => {}
        );
        if(res.data.flag=='1'){
            alert(res.data.msg)
            this.user.name='';
            this.user.email='';
            this.selected='';
            this.user.message='';
            this.selectedFile='';
             $('#modal').click()
        }
         else {
        
            alert(res.data.msg)
         }
       }
    },
     validEmail:function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
    },
    next() {
      this.$refs.slick.next();
    },
    prev() {
      this.$refs.slick.prev();
    },
    reInit() {
      this.$nextTick(() => {
        this.$refs.slick.reSlick();
      });
    },
    Viewmore(slug) {
      this.$router.push("/service/" + slug);
    },
  },
};
</script>

<style>
/* Next & previous buttons */
.prev,
.next {
  background: none;
  border: none;
  top: 50%;
}
.author {
  color: #403e3f;
  font-size: 20px;
}
/* Position the "next button" to the right */
.next {
  position: absolute;
  right: 0;
}
.prev {
  position: absolute;
  left: 0;
}
.next:focus {
  outline: none;
}
.prev:focus {
  outline: none;
}
.left-arrow {
  width: 30px;
}
.right-arrow {
  width: 30px;
}
.line-blue {
  color: #056ab1 !important;
  text-align: center !important;
  border: 3px solid;
  width: 8%;
}
.set-length {
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
</style>