<template>
  <div>
    <!-- Navigation -->
    <div class="short-header">
      <Header />
      <div class="container pt-5">
        <div class="mt-5 pb-5">
          <h1 class="h-t-h mt-5 m-b-3 text-center text-light">Current Job Openings</h1>
        </div>
      </div>
    </div>

    <!-- our mission -->
    <div class="container pb-5 mb-5">
      <div class="row">
        <div class="col-md-12">
          <h3 class="mt-5 mb-4 font-weight-bold text-left text-uppercase">
            We are now hiring for below positions
          </h3>
        </div>
        <div class="col-md-12 job-item light-shadow p-4 mb-3" v-for="(item, index) in careersdata" :key="index">
          <h4 class="mt-1 pb-1 font-weight-bold text-left">
            <span>{{item.career_title}}</span>
            <button class="btn btn-primary btn-sm float-right res-fot-s" data-toggle="modal" data-target="#exampleModal">Apply for this job</button>
          </h4>
          <div class="row job-info">
            <div class="col-md-4">
              <p class="mb-1"><b>Required Skills</b></p>
              <p class="s-f-j-o" >
                <span v-for="(items, index) in item.skill_name" :key="index">
                  {{items.name}}
                  </span>
              </p>
            </div>
            <div class="col-md-2">
              <p class="mb-1"><b>Experience</b></p>
              <p>{{item.experience}}</p>
            </div>
            <div class="col-md-2">
              <p class="mb-1"><b>Qualification</b></p>
              <p>{{item.qualification}}</p>
            </div>
            <!-- <div class="col-md-2">
              <p class="mb-1"><b>Salary</b></p>
              <p>{{item.min_salary}}-{{item.max_salary}}</p>
              <p></p>
            </div> -->

            <div class="col-md-2">
              <p class="mb-1"><b>Type</b></p>
              <p>Full time</p>
            </div>
          </div>
          <!-- <hr class="text-blue line-w my-1" /> -->
          <p class="n-j-t mt-2">
            {{item.description}}
          </p>                  
        </div>

        <!-- Modal -->
      <div class="modal fade" id="exampleModal"  tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title  text-uppercase l-h-35 font-weight-bold" id="exampleModalLabel">Apply For Job</h5>
              <button type="button" class="close" data-dismiss="modal" id="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
           <div class="p-3">
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Full Name" v-model="user.name" 
              />
              <p class="red-color-font-s mb-0" v-if="username!=''">{{username}}</p>
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Your Email" v-model="user.email"
              />
               <p class="red-color-font-s mb-0" v-if="useremail!=''">{{useremail}}</p>
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Mobile" v-model="user.mobile"
              />
               <p class="red-color-font-s mb-0" v-if="usermobile!=''">{{usermobile}}</p>
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Experience" v-model="user.experience"
              />
               <p class="red-color-font-s mb-0" v-if="userexperience!=''">{{userexperience}}</p>
             <div>
              <!-- <select v-model="selected" class="w-100 bg-grew rounded border-none-foucs p-2 my-2">
                <option disabled value="">Skill</option>
                <option v-for="(item, index) in skilldata" :key="index"> {{ item.skill_name }}</option>
              </select> -->
              <multiselect  v-model="value" tag-placeholder="Add this as new tag" placeholder="Search or add a tag" label="name" track-by="code" :options="options" :multiple="true" :taggable="true" @tag="addTag"></multiselect>
            </div>
             <p class="red-color-font-s mb-0" v-if="userselected!=''">{{userselected}}</p>
               <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Current Location - Optional" v-model="user.location"
              />
              
               <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Current Employer - Optional" v-model="user.employer"
              />
              
               <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Notice Period - Optional" v-model="user.noticeperiod"
              />
               
               <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Current Salary - Optional" v-model="user.currsalary"
              />
              
               <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Expected Salary - Optional" v-model="user.expsalary"
              />
              
               <input
                type="file"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                accept=".doc,.docx,application/pdf,application/vnd.ms-excel,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document" v-on:change="onFileChanged"
              />
               <p class="red-color-font-s mb-0" v-if="usersfileelected!=''">{{usersfileelected}}</p>
              <button v-on:click="submit()" class=" btn h-t-b-s my-2 px-5">Submit</button>
            </div>
            <!-- <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">Close</button>
              <button type="button" class="btn btn-primary" data-dismiss="modal">Submit</button>
            </div> -->
          </div>
        </div>
      </div>
      </div>
    </div>
    <div class="d-block bg-odd">
      <div class="container">
        <div class="row">
          
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";
Vue.use(VueAxios, axios);
import Multiselect from 'vue-multiselect'
import $ from 'jquery'
Vue.component('multiselect', Multiselect)

export default {
  name: "Jobopening",
  data() {
    return {
        careersdata:[],
        skilldata:[],
        user: {},
        selected: '',
        username: '',
        useremail: '',
        usermobile: '',
        userexperience: '',
        userselected: '',
        
        usersfileelected: '',
       value: null,
      options: [],
      valuesarray:[],
    };
  },
  mounted() {
     this.JObopeningData();
     this.getSkills();
  },
  components: {
    Header,
    Footer,
    Multiselect,
  },
    methods: {
      addTag (newTag) {
      const tag = {
        name: newTag,
        code: newTag.substring(0, 2) + Math.floor((Math.random() * 10000000))
      }
      this.options.push(tag)
      this.value.push(tag)
    },
        async JObopeningData() {
        var param = "lang=" + "0";
        let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.careers_dataAPI,param,() => {});
        this.careersdata = res.data.data;
      },
      async getSkills() {
      // if(typeof(Vue.prototype.$SkillsData) == "undefined") {
        var param = "lang=" + "0";
        let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.job_skills_dataAPI,
          param,
          () => {}
        );
        // Vue.prototype.$SkillsData = res.data.data;
        this.options = res.data.data;
        for(var i = 0; i < this.options.length; i++){
            this.options[i].name=this.options[i].skill_name;
            this.options[i].code=this.options[i].job_skill_id;
            delete this.options[i].skill_name;
            delete this.options[i].job_skill_id;
        }
      // }
      // else {
      //    this.options = Vue.prototype.$SkillsData;
      //   for(var j = 0; j < this.options.length; j++){
      //       this.options[j].name=this.options[j].skill_name;
      //       this.options[j].code=this.options[j].job_skill_id;
      //       delete this.options[j].skill_name;
      //       delete this.options[j].job_skill_id;
      //   }
      // }
    },
      onFileChanged(event) {
      let input = event.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          this.user.photo = e.target.result;
          this.selectedFile = input.files[0];
        };
        reader.readAsDataURL(input.files[0]);
      }

      // this.selectedFile = event.target.files[0];
    },
    async submit(){
      this.username = "";
      this.useremail= "";
      this.usermobile= "";
      this.userexperience= "";
      this.userselected= "";
      this.usersfileelected= "";
     
       if (!this.user.name) {
          this.username ='This field is required';
       }
       if (!this.user.email) {
          this.useremail ='This field is required';
       }
        else if(!this.validEmail(this.user.email)) {
          this.useremail = 'Not a valid Email';
       }
       if (!this.user.mobile) {
          this.usermobile ='This field is required';
       }
        if (!this.user.experience) {
          this.userexperience ='This field is required';
       }
       if (!this.value || this.value.length=='0') {
          this.userselected ='This field is required';
       }
       if (!this.selectedFile) {
          this.usersfileelected ='This field is required';
       }
       if ( this.username == "" 
       && this.useremail == "" 
       && this.usermobile == "" 
       && this.userexperience == "" 
       && this.userselected == "" 
       && this.usersfileelected == "") {
      let formData = new FormData();
      formData.append("full_name", this.user.name);
      formData.append("email", this.user.email);
      formData.append("mobile_no", this.user.mobile);
      formData.append("experience", this.user.experience);
     
      this.valuesarray=[];
     for(var j = 0; j < this.value.length; j++){
            this.valuesarray.push(this.value[j].code)
     }
     formData.append("skill_id", this.valuesarray);
      formData.append("current_location", ((this.user.location==null)?'':this.user.location));
      formData.append("current_employer", ((this.user.employer==null)?'':this.user.employer));
      formData.append("notice_period", ((this.user.noticeperiod==null)?'':this.user.noticeperiod));
      formData.append("current_salary", ((this.user.currsalary==null)?'':this.user.currsalary));
      formData.append("expected_salary", ((this.user.expsalary==null)?'':this.user.expsalary));
      formData.append("lang", '0');
      if (this.selectedFile) {
        formData.append("resume", this.selectedFile);
      }
      let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.add_apply_jobAPI,
          formData,
          () => {}
        );
        // console.log(res.data);
        if(res.data.flag=='1'){
        alert(res.data.msg)
        this.user={};
        this.selectedFile='';
        $('#modal').click()
       }
       }
    },
     validEmail:function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
    }
   },
};
</script>

<style>
.font-size {
  font-size: 20px;
  color: black !important;
}
.s-f-j-o span {
  font-size: 14px;
  background-color: #056ab1;
  color: white;
  padding: 5px 8px;
  margin-right: 5px;
}
.job-item {
  
}
.job-info {
  border: 1px solid #dddddd;
  margin-left: 0px;
  margin-right: 0px;
  padding-top: 10px;
  border-radius: 10px;
}
.n-j-t{
  color: #7e8081;
  font-size: 18px;
}
.multiselect__tag{
  background: #056ab1;
}
.multiselect__option--highlight{
  background: #056ab1;
}
.multiselect__option--selected.multiselect__option--highlight{
  background: #056ab1;
}
.multiselect__tag-icon:after{
  color:white;
}
</style>