<template>
  <div>
    <!-- Navigation -->
    <div class="short-header">
      <Header />
      <div class="container pt-5">
        <div class="mt-5 pb-5">
          <h1 class="h-t-h mt-5 m-b-3 text-center text-light">
            Articles by our team
          </h1>
        </div>
      </div>
    </div>
    <div class="d-block ">
      <div class="container">
        <div class="row mt-5 mb-5">
          <div class="col-md-12">
            
               <router-link to="/blog">
                <button class="mt-5 my-3 btn bg-secondary text-light px-4">BACK</button>
               </router-link>
           
            <div class="row">              
              <div class="col-md-12 bg-light blog-item mb-3 p-4">                
                <h4 class="mt-1 pb-1 font-weight-bold text-left">
                 {{blogdata.title}}
                  <p class="blog-meta mt-1 mb-0">By {{blogdata.posted_by}} on {{blogdata.blog_day}} {{blogdata.blog_date}}</p>         
                </h4>
                <hr class="text-blue line-w my-1" />  
                <p class="n-b-t mt-2">
                 {{blogdata.description}}
                </p>                  
              </div>
            </div>        
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";

Vue.use(VueAxios, axios);

export default {
  name: "Blog",
  data() {
    return {
       blogdata: [],
    };
  },
  components: {
    Header,
    Footer
  },
  mounted() {
    this.BlogDetailsMore();
  },

  methods: {
   
    async BlogDetailsMore() {
      this.nodata='';
      var param = "lang=" + "0"+"&slug=" + this.$route.params.id;
      let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.blog_detailAPI,param,() => {});
      if(res.data.flag=='1'){
        this.blogdata=res.data.data
      }
    },
  }
};
</script>

