<template>
  <div id="mydiv">
    <!-- Navigation -->
    <div class="short-header">
      <Header />
      <div class="container pt-5">
        <div class="mt-5 pb-5">
          <h1 class="h-t-h mt-5 m-b-3 text-center text-light">
            {{title}}
          </h1>
        </div>
      </div>
    </div>
    <!-- Page Content -->
    <!-- Web devlopment start -->
    <div class="container">
      <div class="row">
        <div class="col-lg-12 text-center">
          <div class="row my-4">
            <div class="col-md-12">
              <h3 class="mt-5 font-weight-bold s-head text-left">
                {{title}}
              </h3>
              <p class="n-d-t mt-4 text-left">
               {{description}}
              </p>
              <div class="text-left">
                <button class="btn h-t-b-s btn-shadow" data-toggle="modal" data-target="#exampleModal1">REQUEST QUOTE</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
      <div class="modal fade" id="exampleModal1"  tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title  text-uppercase l-h-35 font-weight-bold" id="exampleModalLabel">REQUEST QUOTE</h5>
              <button type="button" class="close" data-dismiss="modal" id="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
           <div class="p-3">
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Your Name" v-model="user.name" 
              />
              <p class="red-color-font-s mb-0" v-if="username!=''">{{username}}</p>
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Your Email" v-model="user.email"
              />
               <p class="red-color-font-s mb-0" v-if="useremail!=''">{{useremail}}</p>
             <div>
              <select v-model="selected" class="w-100 bg-grew rounded border-none-foucs p-2 my-2">
                <option disabled value="">Please select one</option>
                <option v-for="(item, index) in Servicedata" :key="index" v-bind:value="item.service_id"> {{ item.title }}</option>
                <option value="0">Not sure</option>
              </select>
            </div>
             <p class="red-color-font-s mb-0" v-if="userselected!=''">{{userselected}}</p>
              <textarea
                rows="4"
                cols="50"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Type your Message here" v-model="user.message"
              ></textarea>
               <p class="red-color-font-s mb-0" v-if="usermessage!=''">{{usermessage}}</p>
               <input
                type="file"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                accept=".doc,.docx,application/pdf,application/vnd.ms-excel,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document" v-on:change="onFileChanged"
              />
               <p class="red-color-font-s mb-0" v-if="usersfileelected!=''">{{usersfileelected}}</p>
              <button v-on:click="submit()" class=" btn h-t-b-s my-2 px-5">Submit</button>
            </div>
            <!-- <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">Close</button>
              <button type="button" class="btn btn-primary" data-dismiss="modal">Submit</button>
            </div> -->
          </div>
        </div>
      </div>

<!--  -->
  <div class="d-block mt-5 pb-5 bg-odd">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 text-center" v-for="(item, index) in labels" :key="index">
            <h1 class="mt-5 font-weight-bold text-left font-s-small mb-5">{{item.title}}</h1>
            <div class="row mt-2">
              <div class="col-md-12">
                <div class="row">
                  <div class="col-md-3 py-2 px-2" v-for="(skill, index) in item.skills" :key="index">
                    <div class="w-w-d-s">
                      <div class="f-h-i h-150">
                        <img v-bind:src="skill.icon" />
                      </div>
                      <h4 class="mt-4 mb-4 break-line-title text-uppercase l-h-35 font-weight-bold">
                        {{skill.name}}
                      </h4>
                      <p class="mb-0 set-length">
                        {{skill.description}}
                      </p>
                      <div class="my-3">
                          <button class="btn h-t-b-s btn-shadow" v-on:click="ViewDetails(skill)" data-toggle="modal" data-target="#exampleModal">View Details</button>
                      </div>
                      
                    </div>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
      </div>
      <!-- <h3 class="see-all-color text-center py-4 mb-0">See all</h3> -->
    </div>

<!--  -->
<!-- Modal -->
      <div class="modal fade" id="exampleModal"  tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h3 class="modal-title  text-uppercase l-h-35 font-weight-bold" id="exampleModalLabel">{{name}}</h3>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              {{descriptions}}
            </div>
            <a v-bind:href="link" target="blank" class="w-25 ml-3 mb-3 btn h-t-b-s btn-shadow" >View Link</a>
          </div>
        </div>
      </div>

    <Footer />
  </div>
</template>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";
import $ from 'jquery'

Vue.use(VueAxios, axios);

export default {
  name: "Webdevlopment",
  data() {
    return {
      selected: '',
      username: '',
      useremail: '',
      usersfileelected: '',
      userselected: '',
      usermessage: '',
      user: {},
      Servicedata: [],
       Servicedetail: [],
       labels: [],
       skills: [],
       title: '',
       name: '',
       link: '',
       icon: '',
       descriptions: '',
       description: '',
    };
  },
  components: {
    Header,
    Footer
  },
  mounted() {
    this.ServiceDetails();
    this.getServices();
  },
  watch: {
    $route () {
      this.ServiceDetails();
    }
},
  methods: {
    async ServiceDetails() {
    var param = "lang=" + "0"+"&slug=" + this.$route.params.id;
    let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.service_detailAPI,param,() => {});
    this.Servicedetail = res.data.data;
    this.title=this.Servicedetail[0].title
    this.description=this.Servicedetail[0].description
    this.labels=this.Servicedetail[0].labels
    },
    ViewDetails(data){
      this.name=data.name
      this.link=data.link
      this.icon=data.icon
      this.descriptions=data.description
    },
     async getServices() {
      if(typeof(Vue.prototype.$servicesData) == "undefined") {
        var param = "lang=" + "0";
        let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.services_dataAPI,
          param,
          () => {}
        );
        Vue.prototype.$servicesData = res.data.data;
        this.Servicedata = Vue.prototype.$servicesData;
      }
      else {
        this.Servicedata = Vue.prototype.$servicesData;
      }
    },
     onFileChanged(event) {
      let input = event.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          this.user.photo = e.target.result;
          this.selectedFile = input.files[0];
        };
        reader.readAsDataURL(input.files[0]);
      }

      // this.selectedFile = event.target.files[0];
    },
      async submit(){
      this.username = "";
      this.useremail = "";
      this.userselected = "";
      this.usermessage = "";
      this.usersfileelected = "";

       if (!this.user.name) {
          this.username ='Please Enter Your Name';
       }
       if (!this.user.email) {
          this.useremail ='Please Enter Your Email';
       }
        else if(!this.validEmail(this.user.email)) {
          this.useremail = 'Not a valid Email';
       }
       if (!this.selected) {
          this.userselected ='Please Select';
       }
        if (!this.selectedFile) {
          this.usersfileelected ='Please Upload File';
       }
       if (!this.user.message) {
          this.usermessage ='Please Enter Message';
       }
       if ( this.username == "" && this.useremail == "" && this.userselected == "" && this.usersfileelected == "" && this.usermessage == "") {
      let formData = new FormData();
      formData.append("name", this.user.name);
      formData.append("email", this.user.email);
      formData.append("lang", '0');
      formData.append("service_id", this.selected);
      formData.append("message", this.user.message);
     
      if (this.selectedFile) {
        formData.append("doc_file", this.selectedFile);
      }
      // console.log(formData);
       let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.add_quoteAPI,
          formData,
          () => {}
        );
        if(res.data.flag=='1'){
            alert(res.data.msg)
            this.user.name='';
            this.user.email='';
            this.selected='';
            this.user.message='';
            this.selectedFile='';
             $('#modal').click()
        }
         else {
            alert(res.data.msg)
         }
       }
    },
     validEmail:function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
    }
  }
};
</script>

<style>
.btn-shadow {
  box-shadow: grey 2px 2px 5px;
}
.see-all-color{
    color: #056ab2;
    text-decoration: underline;
    font-weight: 550!important;
}
.font-s-small{
    font-size: 1.9rem;
}

.set-length {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
#mydiv {
   
    position:inherit;
    top: 0
}
.border-none{
  border: none;
}
.break-line-title{
      overflow-wrap: break-word;
}
</style>