<template>
  <div>
      <footer>
      <div class="d-block bg-blue">
        <div class="container">
          <div class="row pt-5 pb-5">
            <div class="col-md-4">
              <h2>Contact Us</h2>
              <div class="row mt-4">
                <div class="col-md-12">
                  <div class="c-u-list ml-3 mr-3">
                    <div class="row d-table w-100p">
                      <div class="d-table-cell w-18p v-align-top">
                        <img src="../assets/images/location.png" />
                      </div>
                      <div class="d-table-cell w-82p">
                        <p>
                          521-522-523, 5th floor Amby-Vally Arcade, Nr Manisha
                          Garnala, Opp Santosa Heights, Utran, Surat-394105,
                          Gujarat, India.
                        </p>
                      </div>
                    </div>
                    <div class="row d-table w-100p mt-2">
                      <div class="d-table-cell w-18p">
                        <img src="../assets/images/phone-ol.png" />
                      </div>
                      <div class="d-table-cell w-82p">
                        <p>+91 9375519301</p>
                      </div>
                    </div>
                    <div class="row d-table w-100p mt-4">
                      <div class="d-table-cell w-18p">
                        <img src="../assets/images/email.png" />
                      </div>
                      <div class="d-table-cell w-82p">
                        <p>info@dignizant.com</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4 f-o-s">
              <h2>Our Services</h2>
              <div class="row mt-4">
                <div class="col-md-12">
                  <ul class="f-list" v-for="(item, index) in Servicedata" :key="index" v-on:click="Servicelinks(item.slug)">
                    <li class="pointer">{{item.title}}</li>
                  </ul>
                </div>
                <div class="col-md-12 mt-3">
                  <div class="row mr-5 ml-1 f-s-o">
                    <div class="col">
                      <a
                        href="https://www.facebook.com/dignizant/"
                        target="_blank"
                        rel="noopener nofollow noreferrer"
                        ><img src="../assets/images/fb.png"
                      /></a>
                    </div>
                    <div class="col">
                      <a
                        href="https://twitter.com/dignizant"
                        target="_blank"
                        rel="noopener nofollow noreferrer"
                        ><img src="../assets/images/twitter.png"
                      /></a>
                    </div>
                    <div class="col">
                      <a
                        href="https://www.linkedin.com/company/dignizant-technologies/about/"
                        target="_blank"
                        rel="noopener nofollow noreferrer"
                        ><img src="../assets/images/linkden.png"
                      /></a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4 f-o-s">
              <h2>Quick Links</h2>
              <div class="row mt-4">
                <div class="col-md-12">
                  <ul class="f-list">
                     <router-link to="/about-us">
                    <li class="text-light">About Us</li>
                     </router-link>
                      <router-link to="/portfolio">
                    <li class="text-light">Portfolio</li>
                      </router-link>
                      <!-- <router-link to="/life-at-dignizant">
                    <li class="text-light"> Life @Dignizant</li>
                      </router-link> -->
                      <router-link to="/job-openings">
                    <li class="text-light">Job Openings</li>
                      </router-link>
                      <!-- <router-link to="/blog">
                    <li class="text-light">Blog</li>
                      </router-link> -->
                       <router-link to="/contact-us">
                    <li class="text-light">Contact</li>
                       </router-link>
                    <li class="pointer" data-toggle="modal" data-target="#exampleModal">Request Quote</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="d-block bg-black text-center">
        <p class="pt-2 pb-2 mb-0">
          @ Copyright | Dignizant Technologies. All rights reserved
        </p>
      </div>
    </footer>
     <!-- Modal -->
      <div class="modal fade" id="exampleModal"  tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title  text-uppercase l-h-35 font-weight-bold" id="exampleModalLabel">REQUEST QUOTE</h5>
              <button type="button" class="close" data-dismiss="modal" id="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
           <div class="p-3">
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Your Name" v-model="user.name" 
              />
              <p class="red-color-font-s mb-0" v-if="username!=''">{{username}}</p>
              <input
                type="text"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Your Email" v-model="user.email"
              />
               <p class="red-color-font-s mb-0" v-if="useremail!=''">{{useremail}}</p>
             <div>
              <select v-model="selected" class="w-100 bg-grew rounded border-none-foucs p-2 my-2">
                <option disabled value="">Please select one</option>
                <option v-for="(item, index) in Servicedata" :key="index" v-bind:value="item.service_id"> {{ item.title }}</option>
                <option value="0">Not sure</option>
              </select>
            </div>
             <p class="red-color-font-s mb-0" v-if="userselected!=''">{{userselected}}</p>
              <textarea
                rows="4"
                cols="50"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                placeholder="Type your Message here" v-model="user.message"
              ></textarea>
               <p class="red-color-font-s mb-0" v-if="usermessage!=''">{{usermessage}}</p>
               <input
                type="file"
                class="w-100 bg-grew rounded border-none-foucs p-2 my-2"
                accept=".doc,.docx,application/pdf,application/vnd.ms-excel,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document" v-on:change="onFileChanged"
              />
               <p class="red-color-font-s mb-0" v-if="usersfileelected!=''">{{usersfileelected}}</p>
              <button v-on:click="submit()" class=" btn h-t-b-s my-2 px-5">Submit</button>
            </div>
            <!-- <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">Close</button>
              <button type="button" class="btn btn-primary" data-dismiss="modal">Submit</button>
            </div> -->
          </div>
        </div>
      </div>
  </div>
</template>


<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";
import $ from 'jquery'

Vue.use(VueAxios, axios);
export default {
  name: "Footer",

  data() {
      return {
        Servicedata: [],
        selected: '',
      username: '',
      useremail: '',
      usersfileelected: '',
      userselected: '',
      usermessage: '',
      user: {},
       };
  },
  components: {
  },
    mounted() {
      this.getServices();
  },
  methods: {
    async getServices() {      
      if(typeof(Vue.prototype.$home_services) == "undefined") {
        var param = "lang=" + "0";
        let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.home_services_dataAPI,param,() => {});
        Vue.prototype.$home_services = res.data.data;
        this.Servicedata = Vue.prototype.$home_services;
      }
      else {
        this.Servicedata = Vue.prototype.$home_services;
      }
    },
    Servicelinks(id){
     this.$router.push({ name: '/service/'+id}).then((route) => {
        if (route.path == this.$route.path) {
          console.log('Navigation Duplicated');
        }
      })
    this.$router.push('/service/'+id);
    },
     onFileChanged(event) {
      let input = event.target;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          this.user.photo = e.target.result;
          this.selectedFile = input.files[0];
        };
        reader.readAsDataURL(input.files[0]);
      }

      // this.selectedFile = event.target.files[0];
    },
      async submit(){
      this.username = "";
      this.useremail = "";
      this.userselected = "";
      this.usermessage = "";
      this.usersfileelected = "";

       if (!this.user.name) {
          this.username ='Please Enter Your Name';
       }
       if (!this.user.email) {
          this.useremail ='Please Enter Your Email';
       }
        else if(!this.validEmail(this.user.email)) {
          this.useremail = 'Not a valid Email';
       }
       if (!this.selected) {
          this.userselected ='Please Select';
       }
        if (!this.selectedFile) {
          this.usersfileelected ='Please Upload File';
       }
       if (!this.user.message) {
          this.usermessage ='Please Enter Message';
       }
       if ( this.username == "" && this.useremail == "" && this.userselected == "" && this.usersfileelected == "" && this.usermessage == "") {
      let formData = new FormData();
      formData.append("name", this.user.name);
      formData.append("email", this.user.email);
      formData.append("lang", '0');
      formData.append("service_id", this.selected);
      formData.append("message", this.user.message);
     
      if (this.selectedFile) {
        formData.append("doc_file", this.selectedFile);
      }
      // console.log(formData);
       let res = await apiServices.makeAPICall(
          apiServices.postMethod,
          apiServices.URL + apiServices.add_quoteAPI,
          formData,
          () => {}
        );
        if(res.data.flag=='1'){
            alert(res.data.msg)
            this.user.name='';
            this.user.email='';
            this.selected='';
            this.user.message='';
            this.selectedFile='';
             $('#modal').click()
        }
         else {
            alert(res.data.msg)
         }
       }
    },
     validEmail:function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
    }
  }
};
</script>