<template>
  <div>
    <!-- Start Our Latest Work -->
    <div class="d-block pb-5">
      <div class="container">
        <div class="row">
          <div class="col-lg-12 text-center">
            <h1 class="mt-5 font-weight-bold s-head mb-4">
              Our Latest Work
            </h1>   
            <slick ref="slick" class="set-w-slider" :options="slickOptions" v-if="latestWorkData.length > 0">
              <a class="p-1 remove-focus" v-for="(project, project_index) in latestWorkData" :key="project_index">
                <div class="set-container pointer" v-on:click="projectdata(project.slug)">
                  <!-- <img src="../assets/images/images.png" lass="w-100 p-0 " alt="Nature" style="width: 100%"/> -->
                  <img v-if="project.logo!=''" v-bind:src="project.logo" class="w-100 p-0 set-shadows set h-w-fix" alt="Nature" style="width: 100%"/>
                      <img v-if="project.logo==''" src="../assets/images/images.png" class="w-100 p-0 set-shadows set h-w-fix" alt="Nature" style="width: 100%"/>
                  <div class="top-left set-border col-md-8 py-1 px-1 bg-light text-uppercase font-weight-bold pointer" >
                    {{ project.project_title }}
                  </div>
                  <div class="row mt-2 mx-0 w-100 text-block">
                    <div class="col-md-12">
                      <div class="row m-0" v-if="project.skills.length > 0">
                        <div class="text-left">
                            <p class="m-0 p-s-f w-w-d-s p-1 bg-blue font-weight-bold" v-for="(list, skill_index) in project.skills" :key="skill_index">
                              {{ list.name }}
                            </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>    
              </a>
            </slick>          
            <button @click="prev" class="prevs">
              <img
                class="left-arrows"
                src="../assets/images/ic_left_arrow.png"
                alt=""
              />
            </button>
            <button @click="next" class="nexts">
              <img
                class="right-arrows"
                src="../assets/images/ic_right_arrow.png"
                alt=""
              />
            </button>               
          </div>
        </div>
      </div>
    </div>
    <!-- End Our Lastest Work -->
  </div>
</template>

<script>
import Slick from "vue-slick";
import apiServices from "../apiservice";
export default {
  name: "OurLatestWorkSlider",
  data() {
    return {
      latestWorkData: [],
      slickOptions: {
        slidesToShow: 3,
        slidesToScroll: 1,
        infinite: true,
        arrows: false,          
      }
    };
  },
  components: {
    Slick
  },
  mounted() {
    this.getLatestWorkData();
  },
  methods: {
    async getLatestWorkData() {
      var param = "lang=" + "0";
      let res = await apiServices.makeAPICall(
        apiServices.postMethod,
        apiServices.URL + apiServices.latest_work_dataAPI,
        param,
        () => {}
      );      
      this.latestWorkData = res.data.data;
    },
    projectdata(title) {
      this.$router.push("/portfolio-details/" + title);
      // localStorage.projectid = id;
    },
    next() {
      this.$refs.slick.next();
    },
    prev() {
      this.$refs.slick.prev();
    },
    reInit() {
      // Helpful if you have to deal with v-for to update dynamic lists
      this.$nextTick(() => {
        this.$refs.slick.reSlick();
      });
    }
  }
};
</script>

<style>
/* Next & previous buttons */
.prevs,
.nexts {
  background: none;
  border: none;
  top: 60%;
}

/* Position the "next button" to the right */
.nexts {
  position: absolute;
  right: 0;
}
.prevs {
  position: absolute;
  left: 0;
}
.nexts:focus {
  outline: none;
}
.prevs:focus {
  outline: none;
}
.left-arrows {
  width: 30px;
}
.right-arrows {
  width: 30px;
}
.bolder-slider-g{
  border: 10px solid #e4e5e5;
}
.set-container {
  position: relative;
  font-family: Arial;
}
.set-shadows{
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.20) !important;
}
.remove-focus{
  outline: none;
}
.h-w-fix{
  width: 100% !important;
height: 249px !important;
max-height: 249px !important;
}
.set-shadows {
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.2) !important;
}

</style>