                 </router-link>
              </li>
              <li class="nav-item dropdown">
                <a
                  class="nav-link dropdown-toggle pointer"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                  >SERVICES</a
                >
                <div class="dropdown-menu" >
                  <a v-for="(item, index) in Servicedata" :key="index" v-on:click="Servicelinks(item.slug)" class="dropdown-item pointer" >
                      {{item.title}}
                    </a>
                </div>
              </li>
              <li class="nav-item">
                <router-link to="/about-us">
                <a class="nav-link" >ABOUT US</a>
