              </li>
              <li class="nav-item">
                <router-link to="/job-openings">
                <a class="nav-link" >CAREER</a>
                </router-link>
                <!-- <div class="dropdown-menu">
                  <router-link to="/life-at-dignizant" class="dropdown-item pointer">
                  Life @Dignizant
                  </router-link>
                  <router-link to="/job-openings" class="dropdown-item pointer">
                  Job Openings
