<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";

Vue.use(VueAxios, axios);
export default {
  name: "Header",
  data() {
    return {
     Servicedata: [],
    };
  },
  components: {},
  mounted() {
    this.getServices();
  },
  methods: {
    async getServices() {      
      if(typeof(Vue.prototype.$home_services_data) == "undefined") {
        var param = "lang=" + "0";
        let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.home_services_dataAPI,param,() => {});
        Vue.prototype.$home_services_data = res.data.data;
        this.Servicedata = Vue.prototype.$home_services_data;
      }
      else {
        this.Servicedata = Vue.prototype.$home_services_data;
      }
    },
    Servicelinks(id){
     this.$router.push({ name: '/service/'+id}).then((route) => {
        if (route.path == this.$route.path) {
          console.log('Navigation Duplicated');
        }
      })
    this.$router.push('/service/'+id);
    }
  },
 
};
</script>

