<template>
  <div>
    <!-- Navigation -->
    <div class="short-header">
      <Header />
      <div class="container pt-5">
        <div class="mt-5 pb-5">
          <h1 class="h-t-h mt-5 m-b-3 text-center text-light">
            Articles by our team
          </h1>
        </div>
      </div>
    </div>
    <div class="d-block ">
      <div class="container">
        <div class="row mt-5 mb-5">
          <div class="col-md-9">
            <div class="row" v-for="(item, index) in blogdata" :key="index">              
              <div class="col-md-12 bg-light blog-item mb-3 p-4">                
                <h4 class="mt-1 pb-1 font-weight-bold text-left">
                 {{item.title}}
                  <p class="blog-meta mt-1 mb-0">By {{item.posted_by}} on {{item.blog_day}} {{item.blog_date}}</p>         
                </h4>
                <hr class="text-blue line-w my-1" />                
                
                <p class="n-b-t mt-2 set-length">
                 {{item.description}}
                </p>                  
                <a class="b-r-m pointer blue-color" v-on:click="Bloglinks(item.slug)">Read More</a>
              </div>
            </div>
            <div class="text-center" v-if="this.nodata!=''">{{nodata}}</div>            
          </div>
          <div class="col-md-3">
            <div class="input-group md-form form-sm form-2 pl-0">
              <input class="form-control my-0 py-1 lime-border" v-model="searchtext" type="text" placeholder="Search" aria-label="Search"/>
              <div class="input-group-append">
                <span v-on:click="Blog()"
                  class="input-group-text lime lighten-2 bg-blue"
                  id="basic-text1"
                  ><i class="fa fa-search text-grey" aria-hidden="true"
                    ><img src="../assets/images/search.png" alt=""/></i
                ></span>
              </div>
            </div>            
            <div class="my-3 ">
              <div class="bg-blue p-2">Categories</div>
              <div class="bg-odd p-3" >
                <div v-for="(item, index) in blogdatadetails" :key="index">
                   <div class="">{{item.category_name}}</div>
                   <hr class="my-1 text-drak">   
                </div>
                       
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";

Vue.use(VueAxios, axios);

export default {
  name: "Blog",
  data() {
    return {
       blogdata: [],
       searchtext:'',
       nodata:'',
         blogdatadetails: [],
    };
  },
  components: {
    Header,
    Footer
  },
  mounted() {
      this.Blog();
    this.BlogDetails();
  },

  methods: {
    async Blog() {
      this.nodata='';
      var param = "lang=" + "0"+"&search_keyword=" + this.searchtext;
      let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.blogs_dataAPI,param,() => {});
      if(res.data.flag=='1'){
        this.blogdata=res.data.data
      }
      else{
        this.blogdata=[];
        this.nodata=res.data.msg
      }
    },
    async BlogDetails() {
      var param = "lang=" + "0";
      let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.blog_category_dataAPI,param,() => {});
      this.blogdatadetails=res.data.data
    },
    async Bloglinks(id) {
      this.$router.push({ name: '/blog-details/'+id}).then((route) => {
        if (route.path == this.$route.path) {
          console.log('Navigation Duplicated');
        }
      })
    this.$router.push('/blog-details/'+id);
    }
  }
};
</script>

<style>
.btn-shadow {
  box-shadow: grey 2px 2px 5px;
}
.font-s {
  font-size: 12px;
  color: black !important;
}
.bg-grew {
  background-color: #dfdfdf;
}
.border-none-foucs {
  border: none;
  outline: none;
}
.postion-set{
  top: 57px;
    position: absolute;
    left: 15px;
    color: white;
}
.set-w-cal{
  width: 59px;
}
.blog-item {
  border: 1px solid #EEE;
}
.blog-meta {
  font-size: 13px;
  color: #AAAAAA;
}
.b-r-m {
  color: #056ab1;
}
.n-b-t{
  color: #7e8081;
  font-size: 18px;
}
.set-length {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
</style>