<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";

Vue.use(VueAxios, axios);

export default {
  name: "Contactus",
  data() {
    return {
         Contactusdata: [],
          name: null,
          email: null,
          textarea: null,
          username: '',
          useremail: '',
          usertextarea: '',
    };
  },
  components: {
    Header,
    Footer
  },
  mounted() {

  },

  methods: {
    async Contactus() {
       this.username = "";
      this.useremail= "";
      this.usertextarea= "";
       if (!this.name) {
          this.username ='This field is required';
       }
       if (!this.email) {
          this.useremail ='This field is required';
       }
        else if(!this.validEmail(this.email)) {
          this.useremail = 'Not a valid Email';
       }
       if (!this.textarea) {
          this.usertextarea ='This field is required';
       }
        if ( this.username == "" && this.useremail == "" && this.usertextarea == ""){
      var param = "lang=" + "0"+"&name=" + this.name+"&email=" + this.email+"&message=" + this.textarea;
      let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.add_contact_usAPI,param,() => {});
      console.log(res.data,"res.data");
      if(res.data.flag=='1'){
        alert(res.data.msg);
        this.name="";
        this.email="";
        this.textarea="";
      }
        }
    },
     validEmail:function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
    }
  }
};
</script>

