<template>
  <div>
    <!-- Navigation -->
    <div class="protfolio-slider set-h-slider">
      <Header />
      <div class="container pt-5">
        <div class="mt-5 pb-5">
          <h1 class="h-t-h mt-5 m-b-3 text-center text-light">
            Portfolio
          </h1>
        </div>
      </div>
    </div>
    <!-- welcome -->
    <div class="container">
      <div class="row">
        <div class="col-lg-12 text-center">
          <div class="row my-4">
            <div class="col-md-12">
            <span class="alignleft">
               <router-link to="/portfolio">
                <button class="mt-5 btn bg-secondary text-light px-4">BACK</button>
               </router-link>
            </span>
            <h3 class="aligncenter mt-5 font-weight-bold text-uppercase s-head">{{protfliowebproject_title}}</h3>
              <p class="n-d-t mt-4 text-left float-l">
               {{protfliowebdescription}}
              </p>
            </div>
          </div>
        </div>
       
        <div class="d-block mt-5 mb-5 h-t-b w-100 text-center">
            <a v-bind:href="protflioweblink" target="_blank" class="btn h-t-b-s w-w-d-s text-uppercase bg-blue text-light p-2 font-weight-bold w-set">View Link</a>
        </div>
      </div>
    </div>
    <!-- welcome -->
   <!-- image -->
   <div class="bg-odd">
       <div class="container">
           <div>
              <img v-bind:src="protflioweblogo"  class="w-100 my-5">
           </div>
       </div>
   </div>
   <!-- image -->
    <Footer />
  </div>
</template>

<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import apiServices from "../apiservice";
Vue.use(VueAxios, axios);
export default {
  name: "portfoliodetails",
  data() {
    return {
      protfliowebdescription:"",
      protflioweblink:"",
      protfliowebproject_title:"",
      protflioweblogo:"",
    };
  },
  mounted() {
     this.ProtfolioData();
  },
  components: {
    Header,
    Footer,
  },
  methods: {
     async ProtfolioData() {
    var param = "lang=" + "0"+"&slug=" + this.$route.params.id;
    let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.portfolio_detailAPI,param,() => {});
    this.protfliowebdescription = res.data.data.description
    this.protflioweblink = res.data.data.link
    this.protfliowebproject_title = res.data.data.project_title
    this.protflioweblogo = res.data.data.logo
    },
  }
};
</script>

<style>
.protfolio-slider {
  background-image: url("../assets/images/portfoilio.jpg");
  background-size: 100% 100%;
}
.set-h-slider {
  padding-top: 100px;
  padding-bottom: 100px;
}
.alignleft {
    float: left;
    text-align:left;
    width:33.33333%;
}
.aligncenter {
    float: left;
    text-align:center;
    width:33.33333%;
}
.float-l{
    float: left;
}
.w-set{
    width: 150px;
}
</style>